@import 'utils/variables', 'utils/mixins';

.history {
  margin-top: 8px;
}

.header {
  font-weight: 700;
  font-size: 16px;
  text-align: left;
}

.table {
  width: 100%;
}

.row {
  font-size: 14px;

  td,
  th {
    padding: 8px;

    &:first-child {
      padding: 8px 8px 8px 0;
    }

    &:last-child {
      padding: 8px 0 8px 8px;
    }
  }

  th {
    border-bottom: 1px solid $base-dark-gray;
  }

  &:not(:last-child) {
    td,
    th {
      border-bottom: 1px solid $base-dark-gray;
    }
  }
}
