@import 'utils/variables', 'utils/mixins';

.header {
  display: flex;
  align-items: center;

  padding: 8px 0;
  gap: 8px;

  font-weight: 700;
}

.arrow {
  line-height: 0;
  cursor: pointer;
  transition: all 150ms;
  fill: $base-middle-gray;

  svg {
    transition: all 150ms;
    transform: rotate(180deg);
  }

  &.isOpen {
    svg {
      transform: rotate(0deg);
    }
  }

  &:hover {
    fill: $blue-2;
  }
}

.name {
  flex-grow: 1;
  cursor: pointer;
}
