@import 'utils/variables', 'utils/mixins';
.taxpayers {
  &.withLabel {
    padding-top: 8px;
  }
}

.line {
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: $blue-2;

  @include darkTheme {
    background-color: $middle-blue;
  }

  &__top {
    top: 17px;
    left: 0;
  }

  &__left {
    top: 18px;
    bottom: -19px;
    left: 0;
    width: 2px;
    height: auto;
  }

  &__bottom {
    bottom: -20px;
    left: 0;
  }
}

.triangle {
  position: absolute;
  right: -2px;
  top: -5px;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 10px solid $blue-2;

  @include darkTheme {
    border-left-color: $middle-blue;
  }
}

.taxpayer {
  position: relative;
  display: flex;
  gap: 16px;
  padding: 8px 0 8px 32px;
}

.label {
  position: absolute;
  top: -8px;
  font-size: 12px;
  font-weight: 500;
  color: $base-middle-gray;
}
