@import 'utils/variables', 'utils/mixins';

.common {
  margin-bottom: 24px;
}

.nodata {
  padding: 8px 0;
}

.header {
  font-weight: 700;
  font-size: 16px;
  text-align: left;
}

.table {
  width: 100%;
}

.row {
  font-size: 14px;

  td,
  th {
    padding: 8px;

    &:first-child {
      padding: 8px 8px 8px 0;
    }

    &:last-child {
      padding: 8px 0 8px 8px;
    }
  }

  th {
    border-bottom: 1px solid $base-dark-gray;
  }

  &:not(:last-child) {
    td,
    th {
      border-bottom: 1px solid $base-dark-gray;
    }
  }
}

.paginationWrapper {
  display: flex;
  align-items: center;
  justify-content: stretch;
  padding-top: 12px;
}

.total {
  font-size: 14px;
  line-height: 18px;
  white-space: nowrap;
  margin-right: 12px;
}

.pagination {
  width: 100%;
}
