@import 'utils/variables', 'utils/mixins';

.edit {
  margin-bottom: 16px;
}

.header {
  margin-bottom: 16px;
  display: flex;
  justify-content: flex-end;
}

.icon {
  width: 16px;
}

.body {
  border-radius: 4px;
  border: 1px solid $base-dark-gray;
  background: $white;
  padding: 16px;
}

.row {
  display: flex;
  margin-bottom: 16px;
  font-size: 14px;

  &.isDropdown {
    margin-bottom: 42px;
  }
}

.col {
  width: 100%;
}

.buttons {
  display: flex;
  justify-content: space-between;

  padding: 16px 0 0;
}

.files {
  font-size: 14px;
}

.button {
  font-size: 14px !important;
}

.input {
  font-size: 14px !important;
}
